.navigation {
  box-sizing: border-box;
  display: none;
}

.navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation_item {
  z-index: 5;
  color: #fff;
  vertical-align: middle;
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 64px;
  margin: 0 auto;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 13px;
  font-weight: 700;
  display: flex;
  position: relative;
}

.navigation_item:active {
  color: #f4bf87;
  border-bottom: 1px solid #f4bf87;
  justify-content: flex-end;
  height: 42px;
}

.navigation_item.added {
  justify-content: center;
  width: fit-content;
  margin-left: 26px;
}

.burger {
  z-index: 6;
  flex-direction: column;
  justify-content: space-between;
  width: 24px;
  height: 18px;
  display: flex;
  position: relative;
}

.burger span {
  background: #fff;
  border-radius: 2px;
  height: 4px;
  display: block;
}

.header.active .burger span:first-child {
  transform: rotate(45deg)translate(5px, 5px);
}

.header.active .burger span:nth-child(2) {
  z-index: 2;
  opacity: 0;
  display: flex;
  position: relative;
}

.header.active .burger span:nth-child(3) {
  z-index: 2;
  display: flex;
  position: relative;
  transform: rotate(-45deg)translate(5px, -5px);
}

.header.active .navigation {
  z-index: 4;
  box-sizing: border-box;
  background-image: url("bcg.d58dc84a.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 84px 0 170px;
  display: flex;
  position: absolute;
  inset: 0;
}

.header.active .navigation_item.added {
  display: none;
}

.header.active .navigation:after {
  z-index: 0;
  content: "";
  background: linear-gradient(360deg, #edd789e6 0%, #e98283e6 100%);
  width: 100vw;
  height: 100vh;
  position: absolute;
  inset: 0;
}

@media (width >= 768px) {
  .navigation li {
    width: 94px;
    height: 68px;
  }

  .burger {
    display: none;
  }

  .navigation ul {
    display: flex;
  }

  .navigation_item {
    cursor: pointer;
    font-weight: 600;
    list-style: none;
  }

  .navigation {
    z-index: 2;
    color: #fff;
    justify-content: space-between;
    font-size: 13px;
    display: flex;
    position: relative;
  }
}

@media (width >= 1400px) {
  .navigation li {
    width: 114px;
    height: 100px;
  }

  .navigation_item {
    font-size: 14px;
  }

  .navigation {
    z-index: 2;
    position: relative;
  }

  .navigation__svg {
    width: 24px;
    height: 24px;
  }
}
